.stars-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  perspective: 350px;
  opacity: 1;

  animation: fadeInAnimation ease 4s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.shown {
  display: block;
}

.hidden {
  display: none;
}

